body
{
    min-height: 100vh;
    height: auto;
    width: auto;
    padding:none;
    align-items:center;
    display:flexbox;
    justify-content:center;
    background-color: black;
    background-image: url(https://i.imgur.com/WRQJLIW.png);
    background-position:center;
    background-attachment:scroll;
    background-repeat: repeat;
    background-size:cover;
    color: #fff;
    
}
