#admin .container
{
    width:80vh;
    padding: 20px;
}

#admin .container .row100
{
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(autofit,minmax(300px,1fr));

}
#admin .container .row100 .col
{
    position: relative;
    width:100%;
    padding: 0 10px;
    margin:30px 0 10px;
    transition:0.5s; 

}
#admin .container .row100 .inputBox
{
    position: relative;
    width:100%;
    height: 40px;

}

#admin .container .row100 .inputBox input
{
    position: relative;
    width:100%;
    height:100%;
    background: transparent;
    box-shadow: none;
    border: none;
    outline: none;
    font-size: 18px;
    font-family: 'Pixel';
    padding: 0 10px;
    z-index:1 ;
    color: aliceblue;

}

#admin .container .row100 .inputBox .text
{
    position: absolute;
    top: 0;
    left: 0;
    line-height: 40px;
    font-size: 18px;
    font-family: 'Pixel';
    padding: 0 10px;
    display: block;
    transition: 0.5s;
    pointer-events: none;
}

#admin .container .row100 .inputBox input:focus + .text 
{
    top: -35px;
    left: -10px;
}
#admin .container .row100 .inputBox input:valid + .text
{
    top: -35px;
    left: -10px;
}


#admin .container .row100 .inputBox .line
{
    position: absolute;
    bottom: 0;
    display: block;
    width:100%;
    height: 2px;
    transition: 0.5s;
    border: radius 2px; 
    pointer-events: none;
    background-color: aliceblue;
}
#admin .title {
    text-align:center;
    font-family: 'Pixel';
    font-size: 80px;
    color:aliceblue;
    padding: 10px;
    margin-top: 0;
    margin-bottom: 30;
}
#admin .splash{
    text-align:left;
    font-family:'Pixel';
    font-size: 40px;
    color:aliceblue;
    padding: 10px;
    margin: 0px;
}
#admin .btn {
    display:inline-block;
    width: auto;
    clear: both;
    margin: auto;
    block-size: fit-content;
    text-align: center;
    font-family: 'Pixel';
    font-size: 40px;
    text-decoration: none;
    color: var(--color-white);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    border:0px;
    cursor: pointer;
    background:transparent;
    transition: var(--transition); 
}

#admin .btn:hover{
    text-decoration:underline;
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}
