#Signout{
    min-height: 100vh;
    height: auto;
    width: auto;
    padding:none;
    align-items:center;
    display:flexbox;
    justify-content:center;
    background-color: black;
    background-image: url(https://i.imgur.com/bk6m7ts.jpg);
    background-position:center;
    background-attachment:scroll;
    background-repeat: no-repeat;
    background-size:cover;
    color: #fff;
    position:relative;
    
}

#Signout h1 {
    text-align:center;
    font-family: 'Pixel';
    font-size: 80px;
    color:aliceblue;
    padding: 10px;
    margin-top: 0;
    margin-bottom: 30;

}

#Signout h2{
    text-align:center;
    font-family:'Pixel';
    font-size: 40px;
    color:aliceblue;
    padding: 10px;
    margin: 50px;
    
}

#Signout .lg-btn {
    display:inline-block;
    width: auto;
    clear: both;
    margin: auto;
    block-size: fit-content;
    text-align: center;
    font-family: 'Pixel';
    font-size: 40px;
    text-decoration: none;
    color: var(--color-white);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    border:0px;
    cursor: pointer;
    background:transparent;
    transition: var(--transition); 
}

#Signout .lg-btn:hover{
    text-decoration:underline;
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}


#Signout .parent{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    justify-content: space-evenly;
    justify-items: center;
    align-content: space-evenly;
    align-items: center;
}