:root { 
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);
   
    --transition: all 400ms ease;
   
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    --container-height-lg:auto;
   
   }

#cart
{
    min-height: 100vh;
    height: auto;
    width: auto;
    padding:none;
    align-items:center;
    display:flexbox;
    justify-content:center;
    background-color: black;
    background-image: url(https://i.imgur.com/nhXGjm3.jpg);
    background-position:center;
    background-attachment:scroll;
    background-repeat: no-repeat;
    background-size:cover;
    color: #fff;
    
}
#cart .parent {
    font-family: sans-serif;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    color:white;
    justify-content: space-evenly;
  justify-items: center;
  
    }

#cart .totalcart{
color:black;
border-radius: 25px;
background:transparent;
padding: 20px;
width: auto;
height: 350px; 
align-items: center;
}

#cart .items{
color:black;
border-radius: 25px;
background:transparent;
width: auto;
min-width: 75vh;
text-align: left;
}

#cart li{
list-style: none;

}

#cart .total {
    text-align:center;
    font-family: 'Pixel';
    font-size: 40px;
    color:aliceblue;
    padding: 10px;
    margin-top: 0;
    margin-bottom: 30;
   
}

#cart .items{
    text-align:left;
    font-family:'Pixel';
    font-size: 40px;
    color:aliceblue;
    padding: 10px;
    margin: 50px;
}



#cart .btn {
    display:inline-block;
    width: auto;
    clear: both;
    margin: auto;
    block-size: fit-content;
    text-align: right;
    font-family: 'Pixel';
    font-size: 40px;
    text-decoration: none;
    color: var(--color-white);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    border:0px;
    cursor: pointer;
    background:transparent;
    transition: var(--transition); 
}

#cart .btn:hover{
    text-decoration:underline;
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

 #cart .div3
 {
    text-align: center;
 }

