#Signup{
    min-height: 100vh;
    height: auto;
    width: auto;
    padding:none;
    align-items:center;
    display:flexbox;
    justify-content:center;
    background-color: black;
    background-image: url(https://i.imgur.com/bk6m7ts.jpg);
    background-position:center;
    background-attachment:scroll;
    background-repeat: no-repeat;
    background-size:cover;
    color: #fff;
    position:relative;
    
}


#Signup .container
{
    width:80vh;
    padding: 20px;
}

#Signup .container .row100
{
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(autofit,minmax(300px,1fr));

}
#Signup .container .row100 .col
{
    position: relative;
    width:100%;
    padding: 0 10px;
    margin:30px 0 10px;
    transition:0.5s; 

}
#Signup .container .row100 .inputBox
{
    position: relative;
    width:100%;
    height: 40px;

}

#Signup .container .row100 .inputBox input
{
    position: relative;
    width:100%;
    height:100%;
    background: transparent;
    box-shadow: none;
    border: none;
    outline: none;
    font-size: 18px;
    font-family: 'Pixel';
    padding: 0 10px;
    z-index:1 ;
    color: aliceblue;

}

#Signup .container .row100 .inputBox .text
{
    position: absolute;
    top: 0;
    left: 0;
    line-height: 40px;
    font-size: 18px;
    font-family: 'Pixel';
    padding: 0 10px;
    display: block;
    transition: 0.5s;
    pointer-events: none;
}

#Signup .container .row100 .inputBox input:focus + .text 
{
    top: -35px;
    left: -10px;
}
#Signup .container .row100 .inputBox input:valid + .text
{
    top: -35px;
    left: -10px;
}


#Signup .container .row100 .inputBox .line
{
    position: absolute;
    bottom: 0;
    display: block;
    width:100%;
    height: 2px;
    transition: 0.5s;
    border: radius 2px; 
    pointer-events: none;
    background-color: aliceblue;
}

#Signup .title {
    text-align:center;
    font-family: 'Pixel';
    font-size: 80px;
    color:aliceblue;
    padding: 10px;
    margin-top: 0;
    margin-bottom: 30;
   
    
}

#Signup .lg-btn {
    display:inline-block;
    width: auto;
    clear: both;
    margin: auto;
    block-size: fit-content;
    text-align: center;
    font-family: 'Pixel';
    font-size: 40px;
    text-decoration: none;
    color: var(--color-white);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    border:0px;
    cursor: pointer;
    background:transparent;
    transition: var(--transition); 
}

#Signup .lg-btn:hover{
    text-decoration:underline;
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

#Signup .parent{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    justify-content: space-evenly;
    justify-items: center;
    align-content: space-evenly;
    align-items: center;
}